/* google font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap');

/* layout */
:root {
  --primary: #3b9c3b;
  --error: #e7195a;
  --overdue: #fd4e4e;
  --box-shadow: 2px 2px 5px rgba(0,0,0,0.2);
}
body {
  background: #f1f1f1;
  margin: 0;
  font-family: "Poppins";
}
header {
  background: #fff;
  border-bottom-width: 1px;
}
header .container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header a {
  color: #333;
  text-decoration: none;
}
.pages{
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}
label, input {
  display: block;
  font-family: "Poppins";
}
input {
  background: #eeeeee00;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 6px;
  box-sizing: border-box;
}
.loading {
  display: block;
  margin: auto;
  width: 15%;
}

/* homepage */
.home {
  display: flex;
  gap: 10px;
  flex-direction: column;
}
.task-details {
  background: #fff;
  border-radius: 8px;
  margin: 5px auto;
  padding-top: 5px;
  padding-bottom: 3px;
  padding-left: 20px;
  position: relative;
  box-shadow: var(--box-shadow);
}
.task-details h4 {
  margin: 0 0 0px 24px;
  font-size: 1.2em;
  color: #555;
}
.task-details p {
  margin: 0;
  font-size: 0.9em;
  color: #555;
}
.task-details span {
  position: absolute;
  cursor: pointer;
  background: #f1f1f100;
  padding: 6px;
  border-radius: 50%;
  color: #333;
}
.task-details span.delete {
  top: 0px;
  right: 5px;
}
.task-details .edit {
  right: 5px;
  bottom: 5px;
}
.task-details span.checkbox {
  top: 0px;
  left: 5px;
}
.task-details span.tick {
  top: 0px;
  left: 0px;
}
.task-details .hide {
  visibility: hidden;
}
.task-details span.checkbox:hover > .hide {
  visibility: visible;
}

/* new task form */
.task-detail-box {
  background: #dfdfdf;
  border-radius: 4px;
  margin: 20px auto;
  position: relative;
  box-shadow: var(--box-shadow);
}

form button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}
div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid var(--error);
  color: var(--error);
  border-radius: 4px;
  margin: 20px 0;
}
input.error {
  border: 1px solid var(--error);
}

.task-detail-form {
  padding: 10px;
  margin-top: 0px;
  margin-bottom: 0px;
  border: 0px solid #ddd;
  border-radius: 4px;
  margin: 0px auto;
  position: relative;
  outline: none;
}
.task-detail-deadline {
  padding: 10px;
}

/* section form */
.section-form input {
  background: #ffffff;
  box-shadow: var(--box-shadow);
}

/* task-list */
.hide {
  visibility: hidden;
}
.task-section {
  margin-bottom: 20px;
}
.task-section ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}
.task-section ul li {
  cursor: pointer;
  position: relative;
  background: #ffffff;
  box-shadow: var(--box-shadow);
  
  transition-property: height;
  transition-duration: 200ms;
  overflow: hidden;

  /* make the list items unselectable */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.task-section ul li:hover {
  background: #ddd;
}
.task-section ul li:hover > .hide {
  visibility: visible;
}
.task-section ul li.checked {
  background: #888;
  color: #fff;
}
.task-section ul li h4.checked {
  text-decoration: line-through;
}
.task-section ul .overdue {
  color: var(--overdue);
}
.task-section .create-task {
  margin-top: 20px;
}
.task-section h1 {
  margin-bottom: 0px;
}
.add-task {
  display: flex;
  flex-direction: row;
  column-gap: 20px;
}

/* navbar */
nav {
  display: flex;
  align-items: center;
}
nav a {
  margin-left: 10px;
}
nav button {
  background: #fff;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 6px 10px;
  border-radius: 4px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 1em;
  margin-left: 10px;
}
nav .profile-pic {
  width: 36px;
  height: 36px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 5px;
}
nav .username {
  vertical-align: middle;
}
nav .user-details {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 5px;
  cursor: pointer;
}
.links {
  width: 60%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  margin-left: 10px;
  column-gap: 30px;
  margin-right: 20px;
}
nav .logout {
  margin-left: 30px;
}
/* auth forms */
form.signup, form.login {
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}
form.signup ul {
  list-style-position: none;
}
.password-reqs {
  margin-top: -10px;
  font-size: .8em;
  color:#555;
}

.username-reqs {
  margin-top: -10px;
  font-size: .8em;
  color:#555;
}

/* chat */
.chat {
  background: #f1f1f1;
  transform: translateZ(0);
  position: fixed;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  box-shadow: 0 1px 3px hsla(0, 0%, 58.8%, .74);
  bottom: 0;
  right: 6px;
  z-index: 1040;
  width: 620px;
  height: 480px;
}
.chat-closed {
  background: #f1f1f1;
  transform: translateZ(0);
  position: fixed;
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  box-shadow: 0 1px 3px hsla(0, 0%, 58.8%, .74);
  bottom: 0;
  right: 6px;
  z-index: 1040;
  width: 240px;
  height: 30px;
}
.chat-header {
  height: 20px;
  padding: 5px;
  background: var(--primary);
  border-top-right-radius: 4px;
  border-top-left-radius: 4px;
  cursor: pointer;
}
.chat-header h4 {
  margin: 0px;
  padding-left: 5px;
}
.chat-contents {
  width: 100%;
  height: 100%;
  position: relative;
}
.chat-chat .chat-message .timestamp {
  font-size: 12px;
  text-align: right;
}

.chat-channels {
  width: 35%;
  padding-left: 5px;
  position: absolute;
}
.chat-channels ul {
  padding-left: 0px;
  padding-top: 0;
  margin: 0;
  list-style-type: none;
  height: 70%;
  max-height: 70%;
  overflow-y: auto;
}
.chat-channel {
  position: relative;
  display: flow-root;
  background: #ddd;
  padding: 5px;
  border-radius: 6px;
  box-sizing: content-box;
  margin: 5px auto;
  cursor: pointer;
}
.chat-channel:hover {
  background: #ccc;
}
.chat-channel h4,h5 {
  margin: 0;
}
.chat-channel .picture {
  margin-top: 5px;
  width: 38px;
  height: 38px;
  float: left;
}
.chat-channel .picture img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.chat-channel .content {
  width: 77.5%;
  height: 100%;
  left: 30%;
  float: right;
}
.chat-search {
  background: #ddd;
  padding-left: 5px;
  border-radius: 12px;
  margin: auto;
  position: relative;
  box-sizing: border-box;
}
.chat-search input {
  background: #eeeeee00;
  border: 0px;
  width: 85%;
}
.chat-search input:focus {
  outline: none;
}
.chat-search button {
  transform: translate(0, -50%);
  position: absolute;
  cursor: pointer;
  background: #f1f1f100;
  border-radius: 50%;
  top: 50%;
  color: #333;
}
.chat-search .search {
  right: 0px;
}
.chat-chat {
  padding-left: 10px;
  padding-right: 5px;
  position: absolute;
  left: 35%;
  width: 65%;
  height: 100%;
  top: 0;
  box-sizing: border-box;
}

.channel-header h4,h5 {
  margin: 0;
}
.chat-chat .channel-header{
  height: 10%;
  position: relative;
  display: flow-root;
  box-sizing: content-box;
  margin: 5px auto;
}
.chat-chat .channel-header .picture{
  width: 12.5%;
  height: 100%;
  float: left;
}
.chat-chat .channel-header .picture img {
  width: 48px;
  height: 48px;
  object-fit: cover;
  border-radius: 50%;
}
.chat-chat .channel-header .content {
  width: 85%;
  height: 100%;
  float: right;
}

.chat-chat .none-selected {
  text-align: center;
  margin-top: 50%; margin-left: 50%; transform: translate(-50%, -50%)
}

.chat-chat .messages {
  height: 67.5%;
  max-height: 80%;
  overflow-y: auto;
}
.chat-chat ul {
  padding-left: 5px;
  list-style-type: none;
}
.chat-chat .chat-message {
  position: relative;
  display: flow-root;
  box-sizing: content-box;
  margin: 5px auto;
}
.chat-chat .chat-message .picture {
  width: 10%;
  height: 100%;
}
.chat-chat .chat-message .picture img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  border-radius: 50%;
}
.chat-chat .chat-message .content {
  background: #ddd;
  border-radius: 8px;
  padding: 8px;
  width: 84%;
  height: 100%;
}
.chat-chat .chat-message .left {
  float: left;
}
.chat-chat .chat-message .right {
  float: right;
}

.chat-message-form {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 5px;
}
.chat-message-form input {
  width: 100%;
}
.chat-message-form .send {
  margin: 10px;
  height: 100%;
}
.chat-message-form .send img {
  height: 20px;
  width: 24px;
  object-fit: contain;
}

.chat-request-group-btn {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}

/* Styles for the popup form */
.popup-form {
  display: flex;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.4); /* Black with opacity */
}

.popup-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 500px;
  border-radius: 8px;
  position: relative;
}

.close-btn {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 10px;
  right: 20px;
