.toggle-btn {
  position: absolute;
  right: 10px; /* Align the button to the right inside the input */
  border: none;
  background: none;
  cursor: pointer;
  font-size: 14px;
  padding: 0;
}

.toggle-btn .material-symbols-outlined {
  color: #999999; 
}

.profile-pic {
  position: relative;
}

.profile-pic .material-symbols-outlined {
  top: 44%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  font-size: 30px;
  justify-content: center;
  align-items: center;
  opacity: 0; /* Initially hidden */
  transition: opacity 0.3s ease;
}

.profile-pic:hover .material-symbols-outlined {
  opacity: 0.9; /* Show the icon when hovering */
}

.profile-pic:hover img {
  filter: grayscale(75%)
}

