/* group item */
.group-item {
  position: relative;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 12px;
  padding: 10px;
  height: 160px;

}

.group-item .group-header {
  display: flex;
  flex-direction: row;
  vertical-align: top;
  column-gap: 12px;
}

.group-item .group-details {
  width: 100%;
}

.group-item .group-header h2 {
  margin-top: 0;
  margin-bottom: -4px;
}

.group-item .group-pic {
  border-radius: 50%;
  width: 58px;
  height: 58px;
  object-fit: cover;
}

.group-item a {
  text-decoration: none;
  width: 40px;
  text-align: center;
}

.group-item .group-stats {
  display: grid;
  grid-template-columns: 60% 1fr 1fr;
  vertical-align: top;
  column-gap: 12px;
  margin-right: 5px;
}

.group-item .members {
  position: relative;
  vertical-align: center;
}

.group-item .members span {
  position: absolute;
  top: 50%;
  height: 80%;
  margin-left: 4px;
  transform: translate(0, -50%);
}

.group-item .trees-grown {
  position: relative;
  vertical-align: center;
}

.group-item .tree {
  position: absolute;
  top: 50%;
  height: 80%;
  margin-left: 4px;
  transform: translate(0, -50%);
}

.group-item .view {
  position: absolute;
  right: 10px;
  bottom: 10px;
}

/* group page */
.group-page .contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin-bottom: 100px;
}

.group-page h1 {
  margin-top: 4px;
}

.group-page h2, h3 {
  margin-bottom: 4px;
}

.group-page .synctree {
  width: 761px;
  height: 800px;
  position: relative;
}

.group-page .synctree img {
  object-fit: contain;
  transform: translate(-50%, 0);
}

.group-page .synctree h3 {
  text-align: center;
}

.group-page .synctree .tree {
  position: absolute;
  left: 50%;
  height: 70%;
  bottom: 15%;
}

.group-page .synctree .sapling {
  position: absolute;
  left: 50%;
  height: 40%;
  bottom: 12.5%;
}

.group-page .synctree .sprout {
  position: absolute;
  left: 50%;
  height: 15%;
  bottom: 12.5%;
}

.group-page .synctree .soil {
  position: absolute;
  left: 50%;
  height: 15%;
  bottom: 0;
}

.group-page .synctree .comment {
  position: absolute;
  left: 85%;
  height: 30%;
  bottom: 12.5%;
  transform: translate(-50%, 0);
}

.group-page .synctree .comment img {
  height: 100%;
  left: 50%;
  transform: translate(0, 0);
}

.group-page .synctree .comment .message {
  position: absolute;
  top: 10%;
  text-align: center;
  width: 90%;
  height: 42.5%;
  left: 50%;
  transform: translate(-50%, 0);
  overflow-y: auto;
  overflow-x: hidden;
}

.group-page .synctree .comment .sender {
  position: absolute;
  top: 52.5%;
  text-align: right;
  width: 80%;
  height: 10%;
  left: 45%;
  transform: translate(-50%, 0);
}

.group-page .group-actions {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  max-width: 400px;
}

.group-page .group-pic {
  border-radius: 50%;
  width: 140px;
  height: 140px;
  object-fit: cover;
  margin-bottom: -20px;
}

.group-page .title {
  margin-top: 0;
}

.group-members ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  max-height: 400px;
  overflow-y: auto;
}
.group-members ul li {
  position: static;
  background: #ffffff;
  
  transition-property: height;
  transition-duration: 200ms;

  /* make the list items unselectable */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.group-members li {
  position: relative;
  width: 100%;
}

.group-members .member-details {
  margin-bottom: 5px;
  display: grid;
  grid-template-columns: 30px 4fr 1fr;
  column-gap: 10px;
  align-items: center;
}

.kick-actions {
  position: relative;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: right;
}

.group-members .member-details .member-pic {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}

.group-members .search-user {
  display: grid;
  grid-template-columns: 1fr 80px;
  column-gap: 10px;
  align-items: center;
}

.group-members .search-user input {
  margin: 0;
}

.group-members .search-result {
  display: flex;
  grid-template-columns: 1fr 100px;
  column-gap: 10px;
  justify-content: space-between;
  align-items: center;
}

.group-manage .setting {
  display: flex;
  column-gap: 10px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.group-manage .rename-group {
  display: grid;
  grid-template-columns: 1fr 80px;
  column-gap: 10px;
  align-items: center;
}

.group-manage .rename-group input {
  margin: 0;
}

.group-leave .title {
  text-align: center;
}

.leave-actions {
  margin-top: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

div.success {
  padding: 10px;
  background: #fff;
  border: 1px solid var(--primary);
  color: var(--primary);
  border-radius: 4px;
  margin: 20px 0;
}